import React, { useState } from 'react'
import { makeStyles } from '@material-ui/core/styles'
import BasicModal from './BasicModal'
import { skills } from '../static'
import { Typography } from '@material-ui/core'

const useStyles = makeStyles((theme) => ({
  technologies: {
    backgroundColor: '#dedede',
    padding: '45px 16px',
    width: '100%',
    minHeight: '350px',
    color: 'white',
    [theme.breakpoints.down('sm')]: {
      padding: '15px 0'
    }
  },
  title: {
    textAlign: 'center',
    color: 'black'
  },
  listTechs: {
    display: 'flex',
    justifyContent: 'center',
    fontSize: '0.875rem',
    // maxWidth: '700px',
    [theme.breakpoints.down('sm')]: {
      flexDirection: 'column',
      alignItems: 'center',
      padding: 0
    },
    '& li': {
      listStyleType: 'none',
      marginBottom: theme.spacing(1),
      transform: 'scale(1)',
      transition: 'transform 1s',
      cursor: 'pointer',
      padding: theme.spacing(2),
      '& img': {
        maxWidth: '128px',
        maxHeight: '128px',
        [theme.breakpoints.down('sm')]: {
          maxWidth: '98px',
          maxHeight: '98px'
        }
      },
      '&:hover': {
        transform: 'scale(1.45)',
        transition: 'transform 1.5s',
        [theme.breakpoints.down('sm')]: {
          transform: 'scale(1.15)'
        }
      }
    }
  }
}))
export const Technologies = () => {
  const classes = useStyles()
  const [modalTech, setModalTech] = useState(false)
  return (
    <section className={classes.technologies} id='techs'>
      <Typography variant="h4" gutterBottom component="div" className={classes.title}>
        Знания, умения, навыки:
      </Typography>
      <ul className={classes.listTechs}>
        {
          skills.map((skill, i) => {
            return <li
              key={i}
              onClick={() => { setModalTech(skill) }}
            >
              <img src={skill.src} alt="About this skill" />
            </li>
          })
        }
      </ul>
      <BasicModal modalTech={modalTech} setModalTech={setModalTech} />
    </section>

  )
}