import React, { useState } from 'react'
import {
  AppBar,
  Toolbar,
  Typography,
  IconButton,
  Menu,
  MenuItem
} from '@material-ui/core'
import MenuIcon from '@mui/icons-material/Menu'
import { makeStyles } from '@material-ui/core/styles'
import useMediaQuery from '@mui/material/useMediaQuery'
import { HashLink as Link } from 'react-router-hash-link'

const useStyles = makeStyles((theme) => ({
  rootNav: {
    display: 'flex',
    justifyContent: 'center',
    width: '100%',
    height: '100px',
    backgroundColor: '#03143a'
  },
  menuButton: {
    flexGrow: 0
  },
  menuList: {
    '& a': {
      fontSize: '27px'
    },
    '& a.active': {
      fontWeight: 'bold',
      textDecoration: 'underline'
    },
    display: 'flex',
    flexGrow: 0
  },
  title: {
    flexGrow: 1
  }
}))

export const NavbarComponent = () => {
  const [anchorEl, setAnchorEl] = useState(null)
  const classes = useStyles()
  const matches = useMediaQuery('(max-width:768px)')
  const open = Boolean(anchorEl)
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget)
  }
  return (
    <AppBar position='fixed' className={classes.rootNav}>
      <Toolbar>
        <Typography variant='h5' className={classes.title} component='h2'>
          webDevelopment
        </Typography>
        <Menu
          id='menu-appbar'
          anchorEl={anchorEl}
          anchorOrigin={{
            vertical: 'top',
            horizontal: 'right'
          }}
          open={open}
          onClose={() => setAnchorEl(null)}
        >
          {/* <MenuItem onClick={() => handleMenuClick('/')}>Обо мне</MenuItem> */}
          <MenuItem component={Link} to="/#about" onClick={() => setAnchorEl(null)}>Главная</MenuItem>
          <MenuItem component={Link} to="/#techs" onClick={() => setAnchorEl(null)}>Skills</MenuItem>
          <MenuItem component={Link} to="/#prjs" onClick={() => setAnchorEl(null)}>Проекты</MenuItem>
        </Menu>
        {matches ? (
          <IconButton
            edge='start'
            className={classes.menuButton}
            color='inherit'
            area-label='menu'
            onClick={handleClick}
          >
            <MenuIcon />
          </IconButton>
        ) : (
          <>
            <div className={classes.menuList}>
              <MenuItem component={Link} to="/#about">Главная</MenuItem>
              {/* <MenuItem href='https://102nix.github.io/' component='a' target="blank">
                Pet-проект
              </MenuItem> */}
              <MenuItem component={Link} to="/#techs">Skills</MenuItem>
              <MenuItem component={Link} to='/#prjs' >Проекты</MenuItem>
            </div>
          </>
        )}
      </Toolbar>
    </AppBar>
  )
}